:root {
  --black: #000;
  --white: #fff;
  --back-light: #9e9e9e;
  --yellow: #a19f00;
  --grey-light: #2c2c2c;
  --grey-strong: #1d1d1d;
  --btn-blue:#4545b0;
  --btn-red: #a23636;
  --copy-footer: #7e7e7e;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--grey-strong);
  color: var(--white);
  font-family: 'Montserrat', sans-serif;
}

.dark {
  background: linear-gradient(
    to right, #09090a,
    #202020, #09090a);
    background-size: 400% 400%;
    animation: animate-background 7s infinite ease-in-out;
  color: var(--white);
  font-family: 'Montserrat', sans-serif;
}

.light {
  background: linear-gradient(
    to right, #d1d1d1,
    #9a9a9a, #d1d1d1);
    background-size: 400% 400%;
    animation: animate-background 7s infinite ease-in-out;
  color: var(--grey-strong);
  font-family: 'Montserrat', sans-serif;
}

@keyframes animate-background {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

h1 {
  font-family: 'Shadows Into Light', cursive;
  font-size: 50px;
}

p {
  font-size: 19px;
}

html {
  scroll-behavior: smooth;
}

header {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav a {
  color: var(--white);
  text-decoration: none;
  margin: 0 20px 0 20px;
}

nav a:hover {
  color: var(--yellow);
  transition: 400ms;
}

::selection {
  background-color: var(--yellow);
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background-color: var(--grey-light);
  border-radius: 50px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--yellow);
  border-radius: 50px;
}

@media only screen and (max-width: 920px) {
  .sobre {
    display: flex;
    justify-content: center;
    flex-direction: column-reverse;
    margin-top: 100px;
    margin-bottom: 100px;
  }

  .switch-text {
    display: none;
  }

  .hamburguer {
    display: block;
    width: 50px;
    margin-right: 20px;
  }

  .listItems {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .listItems a {
    text-align: center;
    margin: 10px 0;
    color: var(--white);
    background-color: var(--grey-strong);
    padding: 20px;
    border-radius: 20px;
    width: 200px;
  }
  
  .menuClose {
    display: none;
  }

  .div-contato .div-icons-redes {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .sobre .div-icons-redes .button-copy {
    padding: 15px 104px;
  }

  .button-copy {
    padding: 18px 130px;
    margin-bottom: 10px;
    margin-left: 10px;
  }

  .div-icons {
    font-size: 15px;
  }

  .div-icons-habilidades {
    width: 600px;
    display: flex;
  }

  .div-habilidades {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    margin-bottom: 50px;
  }

  .div-text-habilidades {
    margin-left: 40px;
  }

  .sobre .left-raio {
    position: absolute;
    width: 60px;
    left: 0;
    bottom: -95%;
  }

  .div-habilidades .left-raio {
    bottom: -219%;
  }

  .div-contato .left-raio {
    bottom: -350px;
  }

  .left-raio-projetos {
    bottom: -2250px;
  }
}

@media only screen and (max-width: 690px) {
  .div-icons-redes {
    display: flex;
    justify-content: center;
    flex-direction: row;
  }

  .sobre {
    display: flex;
    justify-content: center;
    flex-direction: column-reverse;
  }

  #logo {
    margin-right: 60px;
  }

  nav {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
  }

  nav a {
    margin: 10px;
  }

  .div-habilidades {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 50px;
    margin-bottom: 50px;
  }

  .div-text-projetos {
    width: 400px;
  }

  .projetos {
    width: 400px;
  }

  .projetos:hover {
    width: 500px;
  }

  .descrição-projetos{
    width: 400px;
  }

  .div-logo h3 {
    display: none;
  }

  .div-logo p {
    display: none;
  }

  .comentario {
    width: 400px;
    height: 209px;
  }

  .comentario h2 {
    margin-top: 85px;
  }

  .div-habilidades .left-raio {
    bottom: -180%;
  }

  .div-icons-habilidades {
    width: 500px;
    display: flex;
  }

  .div-habilidades h3 {
    font-size: 40px;
  }

  .icons-habilidades {
    width: 400px;
    height: 80px;
  }
}

@media only screen and (max-width: 530px) {
  .sobre {
    margin-top: 50px;
    margin-bottom: 50px;
  }

  #cartoon {
    width: 320px;
  }

  #cartoon:hover {
    max-width: 350px;
    transition: 400ms;
  }

  .smile-sobre {
    position: absolute;
    margin-top: 80px;
    margin-left: 180px;
    width: 120px;
    height: 90px;
  }

  #logo {
    margin-right: 30px;
  }

  .text-sobre {
    width: 300px;
  }
  
  .text-sobre h2 {
    font-size: 25px;
  }
  
  .text-sobre p {
    font-size: 16px;
  }
  
  .name-sobre {
    display: flex;
  }
  
  .name-sobre h1 {
    font-size: 40px;
  }

  .div-habilidades {
    margin: auto;
    width: 300px;
    margin-top: 50px;
    margin-bottom: 200px;
  }

  .div-text-habilidades {
    margin-left: 0px;
    width: 300px;
  }

  .div-icons-habilidades {
    width: 300px;
    display: flex;
  }

  .div-habilidades h3 {
    font-size: 20px;
    width: 370px;
  }

  .icons-habilidades {
    width: 400px;
    height: 60px;
  }

  .div-icons-redes {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .sobre .div-icons-redes .button-copy {
    padding: 15px 140px;
  }

  .div-bottom-top-img {
    width: 100px;
    margin: auto;
  }

  .sobre .left-raio {
    display: none;
  }

  .div-habilidades .left-raio {
    display: none;
  }

  .left-raio-projetos {
    display: none;
  }

  .div-contato .left-raio {
    display: none;
  }

  .back-top-h {
    margin-top: -150px;
    margin-bottom: 50px;
  }
}